body {
    font-family: sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

main {
    width: 90%;
    margin: 0 auto;
    line-height: 1.5;
    font-size: 16px;
    padding: 3rem 1rem;
    
    @media (min-width: 1280px) {
        width: 1200px;
        font-size: 18px;
        padding-top: 3rem;
        padding-bottom: 3rem;
    }

}


.slip__logo {
    color: #2178FF;
    margin-bottom: 1rem;
}
.heading {
    color: #2178FF;
    margin-top: 0;
    text-align: center;
    font-size: 20px;
    margin-top: 3rem;
    margin-bottom: 2rem;


    .title {
        margin: 0;
    }
}

footer {
    text-align: center;
    padding: 1rem;
    margin-top: 3rem;
}